<template>
  <div class="pa-4">
    <v-avatar v-if="previewUrl" size="300">
      <v-img :src="previewUrl"></v-img>
    </v-avatar>
    <v-file-input
      label="about section photo"
      @change="onFileChange"
    ></v-file-input>
    <form @submit.prevent="submitForm">
      <div class="mb-2">
        <label for="aboutMe">About me</label>
        <v-textarea
          id="aboutMe"
          placeholder="about me"
          type="text"
          hide-details
          outlined
          :rows="5"
          v-model="getSetAboutMe"
        ></v-textarea>
      </div>
      <div
        style="
          display: grid;
          grid-template-columns: 1fr 2fr;
          align-content: center;
        "
        class="mb-2"
      >
        <label for="name">Name</label>
        <v-text-field
          autocomplete="off"
          id="name"
          placeholder="name"
          type="text"
          hide-details
          outlined
          v-model="getSetName"
        />
      </div>
      <div
        style="
          display: grid;
          grid-template-columns: 1fr 2fr;
          align-content: center;
        "
        class="mb-2"
      >
        <label for="location">Location</label>
        <v-text-field
          autocomplete="off"
          id="location"
          placeholder="location"
          type="text"
          hide-details
          outlined
          v-model="getSetLocation"
        />
      </div>
      <div
        style="
          display: grid;
          grid-template-columns: 1fr 2fr;
          align-content: center;
        "
        class="mb-2"
      >
        <label for="phoneNumber">Phone number</label>
        <v-text-field
          autocomplete="off"
          id="phoneNumber"
          placeholder="phoneNumber"
          type="text"
          hide-details
          outlined
          v-model="getSetPhoneNumber"
        />
      </div>
      <v-btn color="primary" type="submit">Save</v-btn>
    </form>
  </div>
</template>

<script>
import { createNamespacedHelpers } from "vuex";
const { mapState, mapMutations, mapActions } =
  createNamespacedHelpers("profile");
export default {
  name: "AboutView",
  data() {
    return {
      file: null,
      previewUrl: null,
    };
  },
  computed: {
    ...mapState(["profile"]),
    getSetName: {
      get() {
        return this.profile && this.profile.about.contactDetails.name;
      },
      set(newVal) {
        this.SET_ABOUT_NAME(newVal);
      },
    },
    getSetAboutMe: {
      get() {
        return this.profile && this.profile.about.aboutMe;
      },
      set(newVal) {
        this.SET_ABOUT_ABOUT_ME(newVal);
      },
    },
    getSetPhoto: {
      get() {
        return this.profile && this.profile.about.photoURL;
      },
      set(newVal) {
        this.SET_ABOUT_PHOTO(newVal);
      },
    },
    getSetLocation: {
      get() {
        return this.profile && this.profile.about.contactDetails.location;
      },
      set(newVal) {
        this.SET_ABOUT_LOCATION(newVal);
      },
    },
    getSetPhoneNumber: {
      get() {
        return this.profile && this.profile.about.contactDetails.phoneNumber;
      },
      set(newVal) {
        this.SET_HERO_PHONE_NUMBER(newVal);
      },
    },
  },
  methods: {
    ...mapMutations([
      "SET_ABOUT_NAME",
      "SET_ABOUT_ABOUT_ME",
      "SET_ABOUT_PHOTO",
      "SET_ABOUT_LOCATION",
      "SET_HERO_PHONE_NUMBER",
    ]),
    ...mapActions(["uploadImage", "fetchProfile", "updateProfile"]),
    async submitForm() {
      if (this.file) {
        const filename = await this.uploadImage(this.file);
        this.SET_ABOUT_PHOTO(filename);
      }
      await this.updateProfile();
      this.file = null;
      this.previewUrl = null;
    },
    onFileChange(e) {
      const data = new FormData();
      data.append("file", e);
      this.file = data;
      this.previewUrl = URL.createObjectURL(e);
    },
  },
  created() {
    this.fetchProfile();
  },
};
</script>

